import "../components/TripStyles.css";
import TripData from "../components/TripData"; // Ensure path is correct.
import Temple from "../assets/2.jpg";
import Modlin from "../assets/3.jpg";
import Singapour from "../assets/5.jpg";

function Trip() {
  return (
    <div className="trip">
      <h1>Recent Trips</h1>
      <p>You can discover destination using Google Maps.</p>
      <div className="tripcard">
        <TripData
          image={Temple}
          heading="Trip to Burma"
          text="The Kuthodaw Pagoda, located in Mandalay, Myanmar, is a prominent Buddhist stupa renowned for housing the 'world's largest book.' Constructed in 1857 by King Mindon Min, the pagoda complex contains 729 marble slabs, each inscribed with portions of the Tipitaka, the sacred Buddhist texts. These slabs are housed in small shrines, collectively forming a monumental repository of Buddhist teachings."
        />

        <TripData
          image={Modlin}
          heading="Trip to Poland"
          text="Poland is a country in Central Europe, known for its rich history, diverse landscapes, and cultural heritage. Its capital is Warsaw, and other major cities include Krakw, Gdask, and Wrocaw. Poland has a mix of medieval architecture, vibrant cities, and natural beauty, including the Tatra Mountains and the Masurian Lake District. "
        />

        <TripData
          image={Singapour}
          heading="Trip to Singapour"
          text="Singapore is a modern city-state and island nation in Southeast Asia, renowned for its rapid development, cultural diversity, and efficient governance. Its capital, Singapore City, is a global financial hub with a skyline dominated by landmarks like Marina Bay Sands, Gardens by the Bay, and the Merlion statue."
        />
      </div>
    </div>
  );
}

export default Trip;
